import React from 'react'
import { Container,Row,Col,Image } from 'react-bootstrap'
import about from "./aboutimage/theatre1.jpg"
import "./about.css"


const About = () => {
    return (
        <div  id="about">
          
            <Container >
            <h1 className="about">About Us</h1>
                <Row>
                    <Col>
                    <Image  fluid src={about}/>
                    </Col>
                    <Col>
                     <h2 className="description">
                     New Empire Cinema is a single screen cinema hall located in Humayun Place in New Market,(opposite Shreeram Arcade),Kolkata,West Bengal,India.This theatre is next to Lighthouse Cinema. New Empire cinema auditorium was set up in 1932. The chief designer architect of this theatre was A.de Bois Shrosbree. At that time this theatre was used to perform Ballets and plays. In the late 1950s this was turned into a complete cinema hall. Under the chairmanship of V. Shantaram, the First International New Youth Film was held in New Empire.
                     </h2>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default About
