import React, { useState, useEffect } from "react";
import "./navbar.css";
import {
  FaFacebookSquare,
  FaInstagramSquare,
  FaYoutubeSquare,
} from "react-icons/fa";
import { GiHamburgerMenu } from "react-icons/gi";
import { IoIosClose } from "react-icons/io";
import { MdSearch } from "react-icons/md";
import { NavLink } from "react-router-dom";
import { HashLink } from 'react-router-hash-link';

import { Button,FormControl } from "react-bootstrap";

const Navbar = () => {
  const [showMediaIcons, setShowMediaIcons] = useState(false);
  const [show, setShow] = useState(false);


  

  const handleScroll = () => {
    const offset = window.scrollY;
    console.log(offset)
    if (offset) {
      setShow(true);
    }
    else {
      setShow(false);
    }
  }




  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () =>  window.removeEventListener("scroll", handleScroll);
  

  }, []);

  

  let navbarClasses = ['main-nav'];
  if (show) {
    navbarClasses.push('show');
  }

  


  return (
    <>
      <nav className={show ? "main-nav show" : "main-nav"} >
        {/* 1st logo part  */}
        <div className="logo">
          <h2>
            <span>N</span>ewempire
            <span>C</span>inema
          </h2>
        </div>

        {/* 2nd menu part  */}
        <div
          className={
            showMediaIcons===true ? "menu-link mobile-menu-link" : "menu-link"
          }>
          <ul>
            <li>
              <NavLink to="/">Home</NavLink>
            </li>
            <li>
              <HashLink   to="#about"
             
              >about</HashLink>
            </li>
            <li>
              <HashLink  to="#movie">movies</HashLink>
            </li>
            <li>
              <HashLink   to="#contact">contact</HashLink>
            </li>
           
           
            
          </ul>
        </div>

        {/* 3rd social media links */}
        <div className="social-media">
          <ul className="social-media-desktop">
          <li>
             
             <Button variant="outline-danger"><MdSearch />Search</Button>
           </li>
          </ul>

          <div className="mobile-search">
          <Button variant="outline-danger"><MdSearch /></Button>
            </div>

          {/* hamburget menu start  */}
          <div className="hamburger-menu">
            <a href="#" onClick={() => setShowMediaIcons(!showMediaIcons)}>
              {showMediaIcons ? <IoIosClose style={{ fontSize: "40px" }} /> : <GiHamburgerMenu />}
              
            </a>
          </div>
        </div>
      </nav>

      {/* hero section  */}
      {/* <section className="hero-section">
        <p>Welcome to </p>
        <h1>Thapa Technical</h1>
      </section> */}
    </>
  );
};

export default Navbar;
