import React,{useState} from "react"
import { Carousel } from "react-responsive-carousel"
import "react-responsive-carousel/lib/styles/carousel.min.css";
import img1 from './sliderImage/romance.png'
import img2 from "./sliderImage/action.png";
import img3 from "./sliderImage/comedy.png";
import img4 from "./sliderImage/adventure.png";
import img5 from "./sliderImage/horror.png";
import img6 from "./sliderImage/science.png";
import img7 from "./sliderImage/book.png";
import "./slider.css"


const Slider = () => {
    const [intervalz, setIntervalz] = useState(1000)

    const onChange = (index, item) => {
        setIntervalz(item.props["data-intervalz"])
    }


    return (
        <div className="carousel-container">
            <Carousel 
            onChange={onChange}
            autoPlay
            interval={intervalz}
            infiniteLoop={true}
            stopOnHover={false}
            showThumbs={false}
            showStatus={false}
            showIndicators={true}
            showArrows={false}
            autoFocus={true}
             
             >
                <div data-intervalz={3000}>
                    <img alt="" src={img1}  />
                  
                </div>
                <div data-intervalz={2000}>
                    <img alt="" src={img2}  />
          
                </div>
                <div data-intervalz={3000}>
                    <img alt="" src={img3} />
                 
                </div>
                <div data-intervalz={3000}>
                    <img alt="" src={img4}  />
                 
                </div>
                <div data-intervalz={3000}>
                    <img alt="" src={img5} />
                 
                </div>
                <div data-intervalz={3000}>
                    <img alt="" src={img6} />
                 
                </div>
                <div data-intervalz={3000}>
                    <img alt="" src={img7}  />
                 
                </div>


            </Carousel>
        </div>
    )
}

export default Slider
