import React from 'react'
import { Container, Row, Col, Form,Button } from 'react-bootstrap'
import "./contact.css"
import video from "./contactvideo/video.mp4"

const Contact = () => {
    return (
        <div id="contact">
            <Container>
                <h1 className="location">Location</h1>
                <Row style={{ backgroundColor: "black" }}>
                    <Col>
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3684.49805127688!2d88.34929351443384!3d22.560468339102602!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3a027708f6bfa841%3A0x680a3dc84cb2c804!2sNew%20Empire%20Cinema!5e0!3m2!1sen!2sin!4v1633242724061!5m2!1sen!2sin" width="100%" height="400vh" style={{ border: "0" }} allowfullscreen="" loading="lazy"></iframe>
                    </Col>
                    <Col>
                        <Form class="contact-form">
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>First Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter First Name" />
                                <Form.Text className="text-muted">
                                  
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Last Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter Last Name" />
                                <Form.Text className="text-muted">
                                  
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Email address</Form.Label>
                                <Form.Control type="email" placeholder="Enter Email" />
                                <Form.Text className="text-muted">
                                  
                                </Form.Text>
                            </Form.Group>

                           
                           <br/>
                            <Button variant="outline-light" type="submit">
                                Submit
                            </Button>
                        </Form>
                    
                </Col>
            </Row>
        </Container>
        </div >
    )
}

export default Contact
